import axios from 'axios'
import recommend from '@/components/recommend.vue'
import winbg from '@/components/winBG.vue'

export default {
  name: 'top',
  components: {
    recommend,
    winbg
  },
  data () {
    return {
      active: '',
      showrecommend: false,
      showBG: false
    }
  },
  methods: {
    changeMenu(menu){
      this.active = menu
      this.$router.push({ path: '/' + menu });
    },
    winBG(){
      this.showBG = !this.showBG
    },
    recommend() {
      this.showrecommend = !this.showrecommend
    }
  }
}
